<template>
  <div id="map"></div>
</template>

<script>
import L from 'leaflet';
import markercluster from 'leaflet.markercluster';

console.log('„ÄñL ==„Äå', L, '„Äç„Äó');
console.log('„Äñmarkercluster ==„Äå', markercluster, '„Äç„Äó');
// let map = {};
const iconsConfig = {
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
};
const icons = {
  // üõà Â∏∏‰∫∫ÂíåÂ∞èÂ≠©Â≠òÈáè
  green: new L.Icon({
    iconUrl:
      'https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-green.png',
    ...iconsConfig,
  }),
  // üõà Â∏∏‰∫∫ÊàñÂ∞èÂ≠©ÁÑ°Â≠òÈáè
  yellow: new L.Icon({
    iconUrl:
      'https://cdn.rawgit.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-yellow.png',
    ...iconsConfig,
  }),
  // üõà Â∏∏‰∫∫ÊàñÂ∞èÂ≠©ÁÑ°Â≠òÈáè
  grey: new L.Icon({
    iconUrl:
      'https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-grey.png',
    ...iconsConfig,
  }),
};

let markers = {};
const markClus = {
  addMap(y, x, item) {
    let mask = {};
    if (item.mask_adult === 0 && item.mask_child === 0) {
      mask = icons.gray;
    } else if (item.mask_adult === 0 || item.mask_child === 0) {
      mask = icons.yellow;
    } else {
      mask = icons.green;
    }

    markers.addLayer(
      L.marker([y, x], {
        icon: mask,
      }).bindPopup(`
      <div class="bdb">
          <h1>${item.name}</h1>
          <h3>
            <span>ÈõªË©±Ôºö</span>
            <a href="tel: ${item.phone}">${item.phone}</a>
          </h3>
          <h3>
            <span>Âú∞ÂùÄÔºö</span>
            <a href="http://www.google.com.tw/maps/place/${item.address}">
              ${item.address}
            </a>
          </h3>
          <h3>
            <span>Êõ¥Êñ∞ÊôÇÈñìÔºö</span>
            ${item.updated}
          </h3>
      </div>
      <p>Êàê‰∫∫Âè£ÁΩ©Êï∏ÈáèÔºö ${item.mask_adult}</p>
      <p>Â∞èÂ≠©Âè£ÁΩ©Êï∏ÈáèÔºö ${item.mask_child}</p>`),
    );
  },
};

export default {
  name: 'home',
  data: () => ({
    data: {},
    map: {},
    markers: {},
    lat: 25.03,
    lon: 121.55,
    leafletID: 0,
  }),
  methods: {
    updateMarker() {
      this.data.forEach((a) => {
        const { properties, geometry } = a;
        markClus.addMap(geometry.coordinates[1], geometry.coordinates[0], properties);
      });
    },
  },
  mounted() {
    // ‚òõTODO: ËºâÂÖ•Âú∞Âúñ
    this.map = L.map('map', {
      center: [this.lat, this.lon],
      zoom: 15,
    });

    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution:
        '&copy; <a target="_blank" href="https://www.openstreetmap.org/">¬© OpenStreetMap Ë≤¢ÁçªËÄÖ</a>',
    }).addTo(this.map);

    // ‚òõTODO: ÂïüÂãïÂ∫ßÊ®ô
    markers = new L.MarkerClusterGroup().addTo(this.map);

    this.$http
      .get('https://raw.githubusercontent.com/kiang/pharmacies/master/json/points.json')
      .then((res) => {
        this.data = res.data.features;
        this.updateMarker(markers);
      });
  },
};
</script>

<style lang="scss">
html,
body,
#map {
  width: 100%;
  height: 100%;
}

.bdb {
  border-bottom: 1px solid #aaa;
}
</style>
